body {
  margin: 0;
  padding: 0;
  min-width: 950px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.w-30 {
  width: 30%!important;
}

.w-70 {
  width: 70%!important;
}

.text-ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.UsersList--last-msg {
  font-size: .9rem;
}

.UsersList--search-title {
  display: block;
  z-index: 1;
  text-align: center;
  font-size: .8rem;
  font-weight: 600;
  margin: 0 auto;
  width: 100px;
  color: #2222228f;
}

.UsersList--search-title:after, .UsersList--search-title:before {
  content: ' ';
  display: inline-block;
  width: 31%;
  margin: 10px 64px;
  position: absolute;
  border-bottom: 1px solid #dee2e6;
}

.UsersList--search-title:after {
  left: -55px;
}

.UsersList--search-title:before {
  right: -55px;
}

.UsersList--items-wrapper .list-group-item.active, .UsersList--items-wrapper .list-group-item:focus, .UsersList--items-wrapper .list-group-item:hover {
  z-index: 0;
}

.UsersList--items-wrapper .list-group-item.active {
  background-color: #2da9e9;
  border-color: #2da9e9;
}

.image_outer_container {
  margin-top: auto;
  margin-bottom: auto;
  border-radius: 50%;
  position: relative;
}

.image_inner_container {
  border-radius: 50%;
  padding: 5px;
  background: #833ab4;
  background: -webkit-linear-gradient(to bottom, #fcb045, #fd1d1d, #833ab4);
  background: -webkit-linear-gradient(top, #fcb045, #fd1d1d, #833ab4);
  background: linear-gradient(to bottom, #fcb045, #fd1d1d, #833ab4);
}

.image_inner_container img {
  height: 100px;
  width: 100px;
  border-radius: 50%;
  border: 5px solid white;
}

.image_outer_container .green_icon {
  background-color: #4cd137;
  position: absolute;
  right: 15px;
  bottom: 5px;
  height: 20px;
  width: 20px;
  border: 5px solid white;
  border-radius: 50%
}

/******************* Accordion Demo - 4 *****************/

#accordion4 .panel {
  border: none;
  border-radius: 0;
  box-shadow: none;
  margin: 0 0 10px;
  overflow: hidden;
  position: relative;
}

#accordion4 .panel-heading {
  padding: 0;
  border: none;
  border-radius: 0;
  margin-bottom: 10px;
  z-index: 1;
  position: relative;
}

#accordion4 .panel-heading:before, #accordion4 .panel-heading:after {
  content: "";
  width: 50%;
  height: 20%;
  box-shadow: 0 15px 5px rgba(0, 0, 0, 0.4);
  position: absolute;
  bottom: 15px;
  left: 10px;
  -webkit-transform: rotate(-3deg);
          transform: rotate(-3deg);
  z-index: -1;
}

#accordion4 .panel-heading:after {
  left: auto;
  right: 10px;
  -webkit-transform: rotate(3deg);
          transform: rotate(3deg);
}

h4.panel-title {
  margin: 10px 0px !important;
}

#accordion4 .panel-title a {
  display: block;
  padding: 0.5rem 1rem;
  margin: 0;
  background: #fff;
  font-size: 1rem;
  font-weight: 500;
  letter-spacing: 1px;
  color: #d11149;
  border-radius: 0;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1), 0 0 40px rgba(0, 0, 0, 0.1) inset;
  position: relative;
}

#accordion4 .panel-title a:before, #accordion4 .panel-title a.collapsed:before {
  content: " ";
  font-weight: 900;
  width: 55px;
  height: 100%;
  text-align: center;
  line-height: 50px;
  border-left: 2px solid #D11149;
  position: absolute;
  top: 0;
  right: 0;
}

.InfoPanel--accordion-icon {
  display: inline-block;
  width: 55px;
  height: 100%;
  font-size: 20px;
  color: rgba(0, 0, 0, 0.7);
  line-height: 50px;
  text-align: center;
  position: absolute;
  top: 0;
  right: 20px;
}

#accordion4 .panel-body {
  padding: 10px 15px;
  margin: 0 0 20px;
  border-bottom: 3px solid #d11149;
  border-top: none;
  background: #fff;
  font-size: 15px;
  color: #333;
  line-height: 27px;
}

.ChatForm--action-btn {
  padding: 0 .5rem;
}

.message-chat {
  width: 100%;
  height: 100%;
}

.no-margin-bottom {
  margin-bottom: 0 !important;
}

.info-combo>h5 {
  margin: 2px 0 6px 0;
}

/* Messages */

.messages-panel img.img-circle {
  border: 1px solid rgba(0, 0, 0, 0.1);
}

.medium-image {
  width: 45px;
  height: 45px;
  margin-right: 5px;
}

.img-circle {
  border-radius: 50%;
}

.messages-panel {
  width: 100%;
  height: calc(100vh - 150px);
  min-height: 460px;
  background-color: #fbfcff;
  display: inline-block;
  border-top-left-radius: 5px;
  margin-bottom: 0;
}

.messages-panel img.img-circle {
  border: 1px solid rgba(0, 0, 0, 0.1);
}

.messages-panel .tab-content {
  border: none;
  background-color: transparent;
}

.message-body {
  background-color: #fbfcff;
  height: 100%;
  width: calc(100% - 305px);
  float: right;
}

.message-body .message-top {
  display: inline-block;
  width: 100%;
  position: relative;
  min-height: 53px;
  height: auto;
  background-color: #fff;
  border-bottom: 1px solid rgba(205, 211, 237, 0.5);
}

.message-body .message-top .new-message-wrapper {
  width: 100%;
}

.message-body .message-top .new-message-wrapper>.form-group {
  width: 100%;
  padding: 10px 10px 0 10px;
  height: 50px;
}

.message-body .message-top .new-message-wrapper .form-group .form-control {
  width: calc(100% - 50px);
  float: left;
}

.message-body .message-top .new-message-wrapper .form-group a {
  width: 40px;
  padding: 6px 6px 6px 6px;
  text-align: center;
  display: block;
  float: right;
  margin: 0;
}

.message-body .message-top>.btn {
  height: 53px;
  line-height: 53px;
  padding: 0 20px;
  float: right;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  margin: 0;
  font-size: 15px;
  opacity: 0.9;
}

.message-body .message-top>.btn:hover, .message-body .message-top>.btn:focus, .message-body .message-top>.btn.active {
  opacity: 1;
}

.message-body .message-top>.btn>i {
  margin-right: 5px;
  font-size: 18px;
}

.chat-body {
  width: 100%;
  height: 100%;
  background-color: #fbfcff;
  margin-bottom: 30px;
  padding: 20px 10px;
}

.message {
  position: relative;
  width: 100%;
}

.message br {
  clear: both;
}

.message .message-body {
  position: relative;
  width: auto;
  max-width: calc(100% - 150px);
  float: left;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #dbe3e8;
  margin: 0 5px 20px 15px;
  color: #788288;
}

.message:after {
  content: "";
  position: absolute;
  top: 11px;
  left: 63px;
  float: left;
  z-index: 100;
  border-top: 10px solid transparent;
  border-left: none;
  border-bottom: 10px solid transparent;
  border-right: 13px solid #fff;
}

.message:before {
  content: "";
  position: absolute;
  top: 10px;
  left: 62px;
  float: left;
  z-index: 99;
  border-top: 11px solid transparent;
  border-left: none;
  border-bottom: 11px solid transparent;
  border-right: 13px solid #dbe3e8;
}

.message .medium-image {
  float: left;
  margin-left: 10px;
}

.message .message-info {
  width: 100%;
  height: 22px;
}

.message .message-info>h5>i {
  font-size: 11px;
  font-weight: 700;
  margin: 0 2px 0 0;
  color: #a2b8c5;
}

.message .message-info>h5 {
  color: #a2b8c5;
  margin: 8px 0 0 0;
  font-size: 12px;
  float: right;
  padding-right: 10px;
}

.message .message-info>h4 {
  font-size: 14px;
  font-weight: 600;
  margin: 7px 13px 0 10px;
  color: #65addd;
  float: left;
}

.message hr {
  margin: 4px 2%;
  width: 96%;
  opacity: 0.75;
}

.message .message-text {
  text-align: left;
  padding: 3px 13px 10px 13px;
  font-size: 14px;
}

.message.my-message .message-body {
  float: right;
  margin: 0 15px 20px 5px;
}

.message.my-message:after {
  content: "";
  position: absolute;
  top: 11px;
  left: auto;
  right: 63px;
  float: left;
  z-index: 100;
  border-top: 10px solid transparent;
  border-left: 13px solid #fff;
  border-bottom: 10px solid transparent;
  border-right: none;
}

.message.my-message:before {
  content: "";
  position: absolute;
  top: 10px;
  left: auto;
  right: 62px;
  float: left;
  z-index: 99;
  border-top: 11px solid transparent;
  border-left: 13px solid #dbe3e8;
  border-bottom: 11px solid transparent;
  border-right: none;
}

.message.my-message .medium-image {
  float: right;
  margin-left: 5px;
  margin-right: 10px;
}

.message.my-message .message-info>h5 {
  float: left;
  padding-left: 10px;
  padding-right: 0;
}

.message.my-message .message-info>h4 {
  float: right;
}

.message.info .message-body {
  background-color: #2da9e9;
  border: 1px solid #2da9e9;
  color: #fff;
}

.message.info:after, .message.info:before {
  border-right: 13px solid #2da9e9;
}

.message.success .message-body {
  background-color: #0ec8a2;
  border: 1px solid #0ec8a2;
  color: #fff;
}

.message.success:after, .message.success:before {
  border-right: 13px solid #0ec8a2;
}

.message.warning .message-body {
  background-color: #ff9e2a;
  border: 1px solid #ff9e2a;
  color: #fff;
}

.message.warning:after, .message.warning:before {
  border-right: 13px solid #ff9e2a;
}

.message.danger .message-body {
  background-color: #f95858;
  border: 1px solid #f95858;
  color: #fff;
}

.message.danger:after, .message.danger:before {
  border-right: 13px solid #f95858;
}

.message.dark .message-body {
  background-color: #314557;
  border: 1px solid #314557;
  color: #fff;
}

.message.dark:after, .message.dark:before {
  border-right: 13px solid #314557;
}

.message.info .message-info>h4, .message.success .message-info>h4, .message.warning .message-info>h4, .message.danger .message-info>h4, .message.dark .message-info>h4 {
  color: #fff;
}

.message.info .message-info>h5, .message.info .message-info>h5>i, .message.success .message-info>h5, .message.success .message-info>h5>i, .message.warning .message-info>h5, .message.warning .message-info>h5>i, .message.danger .message-info>h5, .message.danger .message-info>h5>i, .message.dark .message-info>h5, .message.dark .message-info>h5>i {
  color: #fff;
  opacity: 0.9;
}

@media screen and (max-width: 767px) {
  .messages-panel {
    min-width: 0;
    display: inline-block;
  }
  .message-body {
    width: 100%;
    margin: 20px 0;
    border: 1px solid #dce2e9;
    background-color: #fff;
  }
  .message .message-body {
    max-width: calc(100% - 85px);
  }
  .message-body .chat-body {
    background-color: #fff;
    width: 100%;
  }
}
